@keyframes animate360 {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

@keyframes switch {
  0% {
    left: 4px;
  }

  60% {
    width: 15px;
    left: 4px;
  }

  100% {
    width: 20px;
    left: 35px;
  }
}

@keyframes reverse {
  0% {
    width: 20px;
    left: 35px;
  }

  60% {
    width: 15px;
    left: 4px;
  }

  100% {
    left: 4px;
  }
}

