.toggle--checkbox {
  display: none;
}

.dark-theme {
  background-color: var(--indigo);
  color: var(--white);
}

.dark-theme .movie-card__title, .dark-theme .modal-movie-card__description, .dark-theme .modal-movie-card__film-desсriptoin, .dark-theme .modal-movie-card__table-value, .dark-theme .modal-movie-card__film-title {
  color: var(--white);
}

.dark-theme .modal-movie-card, .dark-theme .modal-movie-card__close-btn {
  background-color: var(--indigo);
}

.dark-theme .modal-movie-card__icon-exit {
  fill: var(--white);
}

.toggle--checkbox:checked + .toggle--label {
  background: var(--indigo);
  border-color: var(--indigo-border);
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background {
  width: 3px;
  height: 3px;
  top: 5px;
  left: 10px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:before {
  width: 3px;
  height: 3px;
  top: 5px;
  left: 15px;
}

.toggle--checkbox:checked + .toggle--label .toggle--label-background:after {
  width: 3px;
  height: 3px;
  top: 12px;
  left: 4px;
}

.toggle--checkbox:checked + .toggle--label:before {
  background: var(--white);
  border-color: var(--gray);
  animation-name: switch;
  animation-duration: .1s;
  animation-fill-mode: forwards;
}

.toggle--label {
  width: 60px;
  height: 30px;
  background: var(--blue-color);
  border: 1px solid var(--blue-border);
  border-radius: 100px;
  transition: all .35s ease-in;
  display: flex;
  position: relative;
}

.toggle--label-background {
  width: 6px;
  height: 6px;
  background: var(--white);
  border-radius: 5px;
  transition: all .15s ease-in;
  position: relative;
  top: 10px;
  left: 35px;
}

.toggle--label-background:before {
  content: "";
  width: 15px;
  height: 3px;
  background: var(--white);
  border-radius: 5px;
  transition: all .15s ease-in;
  position: absolute;
  top: 0;
  left: -9px;
}

.toggle--label-background:after {
  content: "";
  width: 15px;
  height: 3px;
  background: var(--white);
  border-radius: 5px;
  transition: all .15s ease-in;
  position: absolute;
  top: 5px;
  left: 1px;
}

.toggle--label:before {
  content: "";
  width: 20px;
  height: 20px;
  border: 1px solid var(--yellow-border);
  background: var(--yellow-background);
  border-radius: 82px;
  transition: all .35s ease-in;
  animation-name: reverse;
  animation-duration: .1s;
  animation-fill-mode: forwards;
  position: absolute;
  top: 4px;
  left: 4px;
}

.toggle--label:after {
  content: "";
  box-shadow: var(--gray) -13px 0 0 2px, var(--gray) -24px 14px 0 -2px;
  width: 5px;
  height: 5px;
  opacity: 0;
  background: none;
  border-radius: 50%;
  transition: all .25s ease-in;
  position: absolute;
  top: 23px;
  left: 143px;
}

