.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader-wrapper {
  z-index: 2000;
  width: 300px;
  height: 100px;
  position: absolute;
  top: 330px;
  left: 50%;
  transform: translate(-50%);
}

.circle {
  width: 10px;
  height: 10px;
  transform-origin: 50%;
  background-image: linear-gradient(90deg, #ff6b08 0%, #ff001b 74%);
  border-radius: 50%;
  animation: circle .5s infinite alternate;
  position: absolute;
  left: 33%;
}

@keyframes circle {
  0% {
    height: 10px;
    border-radius: 100px 100px 50px 50px;
    top: 30px;
    transform: scaleX(1.4);
  }

  40% {
    height: 10px;
    border-radius: 50%;
    transform: scaleX(1);
  }

  100% {
    top: 0%;
  }
}

.circle:nth-child(2) {
  animation-delay: .2s;
  left: 49%;
}

.circle:nth-child(3) {
  animation-delay: .3s;
  left: auto;
  right: 33%;
}

.shadow {
  width: 10px;
  height: 4px;
  transform-origin: 50%;
  z-index: -1;
  filter: blur(1px);
  background-color: #00000080;
  border-radius: 50%;
  animation: shadow .5s infinite alternate;
  position: absolute;
  top: 32px;
  left: 33%;
}

@keyframes shadow {
  0% {
    transform: scaleX(1.1);
  }

  40% {
    opacity: .7;
    transform: scaleX(1);
  }

  100% {
    opacity: .4;
    transform: scaleX(.2);
  }
}

.shadow:nth-child(5) {
  animation-delay: .2s;
  left: 49%;
}

.shadow:nth-child(6) {
  animation-delay: .3s;
  left: auto;
  right: 33%;
}

@media (min-width: 700px) {
  .loader-wrapper {
    width: 600px;
  }

  .circle {
    width: 15px;
    height: 15px;
  }

  @keyframes circle {
    0% {
      height: 15px;
      border-radius: 100px 100px 50px 50px;
      top: 30%;
      transform: scaleX(1.4);
    }

    40% {
      height: 15px;
      border-radius: 50%;
      transform: scaleX(1);
    }

    100% {
      top: 0%;
    }
  }

  .shadow {
    width: 15px;
    height: 5px;
  }
}

